@import url("https://fonts.googleapis.com/css?family=Cabin&display=swap");
.box {
  width: 100%;
  height: 100%;
  min-width: 100%;
  min-height: calc(100vh - 214px);
  position: relative;
  display: inline-block;
  padding-top: 50px;
  padding-left: 10px;
  padding-right: 10px; }

.error {
  display: block;
  margin-left: auto;
  margin-right: auto;
  max-width: 1200px;
  width: 100%;
  text-align: center;
  height: auto;
  line-height: 1.4; }
  .error h2 {
    font-family: "Cabin", sans-serif;
    font-weight: 700;
    font-size: 35px;
    color: #ff9933;
    text-transform: uppercase;
    letter-spacing: 7px;
    padding-top: 20px;
    padding-bottom: 10px; }
    @media (max-width: 200px) {
      .error h2 {
        font-size: 1.5em; } }
    @media (min-width: 599px) {
      .error h2 {
        font-size: 2em; } }
    @media (min-width: 600px) {
      .error h2 {
        font-size: 2.5em; } }
    @media (min-width: 900px) {
      .error h2 {
        font-size: 3em; } }
    @media (min-width: 1200px) {
      .error h2 {
        font-size: 3.5em; } }
    @media (min-width: 1800px) {
      .error h2 {
        font-size: 4em; } }
  .error p {
    font-family: "Cabin", sans-serif;
    font-size: 16px;
    color: #000;
    font-weight: 400;
    padding-bottom: 10px; }
    @media (max-width: 200px) {
      .error p {
        font-size: 0.5em; } }
    @media (min-width: 599px) {
      .error p {
        font-size: 0.75em; } }
    @media (min-width: 600px) {
      .error p {
        font-size: 1em; } }
    @media (min-width: 900px) {
      .error p {
        font-size: 1.25em; } }
    @media (min-width: 1200px) {
      .error p {
        font-size: 1.5em; } }
    @media (min-width: 1800px) {
      .error p {
        font-size: 1.75em; } }
  .error a {
    font-family: "Cabin", sans-serif;
    display: inline-block;
    padding: 15px 35px;
    background-color: #324353;
    border: none;
    border-radius: 40px;
    color: #fff;
    font-size: 14px;
    font-weight: 700;
    text-transform: uppercase;
    text-decoration: none;
    -webkit-transition: 0.2s all;
    transition: 0.2s all; }
    @media (max-width: 200px) {
      .error a {
        font-size: 0.5em; } }
    @media (min-width: 599px) {
      .error a {
        font-size: 0.75em; } }
    @media (min-width: 600px) {
      .error a {
        font-size: 1em; } }
    @media (min-width: 900px) {
      .error a {
        font-size: 1.25em; } }
    @media (min-width: 1200px) {
      .error a {
        font-size: 1.5em; } }
    @media (min-width: 1800px) {
      .error a {
        font-size: 1.75em; } }
  .error a:hover {
    background-color: #ff9933;
    color: #fff;
    text-decoration: none; }

.icon {
  color: #ff9933;
  font-size: 50px;
  margin-bottom: 20px; }
  @media (max-width: 200px) {
    .icon {
      font-size: 4em; } }
  @media (min-width: 599px) {
    .icon {
      font-size: 4.5em; } }
  @media (min-width: 600px) {
    .icon {
      font-size: 5em; } }
  @media (min-width: 900px) {
    .icon {
      font-size: 5.5em; } }
  @media (min-width: 1200px) {
    .icon {
      font-size: 6em; } }
  @media (min-width: 1800px) {
    .icon {
      font-size: 6.5em; } }
