/* The side navigation menu */
.sidenav {
  height: 100%;
  width: 70%;
  position: fixed;
  z-index: 999;
  top: 0;
  right: 0;
  background-color: #324353;
  overflow-x: hidden;
  padding-top: 60px;
  transition: 0.5s; }
  .sidenav .tabs {
    border-bottom: 0.1px solid #7a7e81; }
    .sidenav .tabs.active {
      color: #bcc3c7; }
  .sidenav a {
    padding: 8px 8px 8px 32px;
    text-decoration: none;
    font-size: 25px;
    color: #ffffff;
    display: block;
    transition: 0.3s; }
    .sidenav a:hover {
      color: #bcc3c7; }
  .sidenav .closebtn {
    position: absolute;
    top: -5px;
    left: -10px;
    font-size: 36px;
    margin-right: 50px; }

.togglebtn {
  font-size: 35px;
  display: block;
  text-align: right;
  color: #fff;
  margin-bottom: 5px;
  cursor: pointer; }

.gray {
  background-color: #324353; }

