.detailed-certificate-block {
  border-radius: 5px;
  border-width: 1px;
  border-style: solid;
  min-width: 340px;
  z-index: 100;
  box-shadow: 2px 2px 4px 0 #c1c9d1; }

.valid-border-color {
  border-color: #28a745; }

.warning-border-color {
  border-color: #ffbf00; }

.invalid-border-color {
  border-color: #e46767; }

@media only screen and (max-width: 550px) {
  .detailed-certificate-block {
    min-width: 300px; } }
