.verifyBlocksContainer {
  position: absolute;
  z-index: 1;
  padding-left: 0 !important;
  padding-right: 0 !important; }

.simple-verify-block {
  width: 100%;
  min-width: 300px;
  border-radius: 5px;
  color: white;
  height: 100%;
  box-shadow: 2px 2px 4px 0 #c1c9d1; }
  .simple-verify-block div {
    padding: 2px 0;
    height: 100%;
    margin: auto 0; }

.arrow {
  height: 100%;
  margin: 6px 15px 0 15px; }

.valid {
  background-color: #28a745;
  border: 1px solid #28a745; }
  .valid:hover {
    box-shadow: 2px 2px 6px 0 #a7afb7; }
  .valid.active {
    color: #28a745;
    background-color: #fff; }
    .valid.active #circle {
      fill: #28a745; }
    .valid.active #check {
      fill: #fff; }
    .valid.active svg > polyline {
      stroke: #28a745; }

.warning {
  background-color: #ffbf00;
  border: 1px solid #ffbf00; }
  .warning:hover {
    box-shadow: 2px 2px 6px 0 #a7afb7; }
  .warning.active {
    color: #ffbf00;
    background-color: #fff; }
    .warning.active #circle {
      fill: #ffbf00; }
    .warning.active #check {
      fill: #fff; }
    .warning.active svg > polyline {
      stroke: #ffbf00; }

.invalid {
  background-color: #e46767;
  border: 1px solid #e46767; }
  .invalid:hover {
    box-shadow: 2px 2px 6px 0 #a7afb7; }
  .invalid.active {
    color: #e46767;
    background-color: #fff; }
    .invalid.active #circle {
      fill: #e46767; }
    .invalid.active #check {
      fill: #fff; }
    .invalid.active svg > polyline {
      stroke: #e46767; }

.verification-text {
  font-size: 1.25rem; }

.verified-icon {
  margin: 0 10px !important; }

@media only screen and (max-width: 575px) {
  .verifyBlocksContainer {
    position: relative; }
  .d-flex {
    display: block !important; }
  .verification-text {
    font-size: 1rem; } }

@media only screen and (min-width: 576px) and (max-width: 979px) {
  .verifyBlocksContainer {
    display: block !important; }
  .d-flex {
    display: block !important; }
  .verification-text {
    font-size: 1rem; } }

@media only screen and (min-width: 980px) {
  .verifyBlocksContainer {
    position: block !important; }
  .d-flex {
    display: -ms-flexbox !important;
    display: flex !important; }
  .verification-text {
    font-size: 1.25rem; } }
