@media print {
  #header-ui,
  #renderer-loader,
  #top-header-ui {
    display: none; }
  iframe {
    height: 99vh;
    /* dont use 100vh otherwise a second blank page appear when printing */ }
  .decentralised-renderer {
    position: static !important; } }

.decentralised-renderer {
  width: 100%;
  border: 0;
  position: relative; }

:global(.frameless-tabs .react-tabs .d-lg-none.d-xl-none) {
  display: none; }

:global(.frameless-tabs .react-tabs .d-none.d-lg-block.d-xl-block) {
  display: none !important; }

#top-header-ui,
#header-ui {
  background-color: #f3f8fc; }

#top-header-ui {
  z-index: 3;
  position: relative;
  padding: 1rem 2rem; }

#header-ui {
  box-shadow: 0 4px 2px -2px rgba(0, 0, 0, 0.2);
  padding: 0 2rem;
  margin-bottom: 25px; }

.header-container {
  max-width: 1280px;
  margin: auto; }

.tab {
  color: #8f8f8f;
  font-family: "Montserrat", sans-serif;
  background-color: transparent;
  border-color: transparent;
  z-index: 1;
  position: relative;
  display: inline-block;
  padding: 1.5em 1.5em 1em;
  -webkit-text-decoration: none;
  text-decoration: none;
  margin: 0 7px;
  cursor: pointer;
  text-transform: uppercase;
  border-bottom: 5px solid #c1c9d1; }
  .tab:hover {
    color: #324353;
    border-bottom: 5px solid #324353; }
  .tab.active {
    pointer-events: none;
    color: #324353;
    font-weight: bold;
    border-bottom: 5px solid #324353; }

#frameless-container .view-another {
  display: none; }

.pd-0 {
  padding-right: 0 !important;
  padding-left: 0 !important; }

.view-another {
  border: 1px solid #324353;
  color: #324353;
  background-color: #324353;
  padding: 7px 23px;
  border-radius: 7px;
  font-weight: 500;
  text-align: center;
  vertical-align: middle;
  min-width: 135px;
  cursor: pointer;
  background-color: #fff;
  margin: auto;
  margin-right: 0;
  float: right;
  margin-top: 15px;
  text-decoration: none !important; }
  .view-another:hover {
    border: 1px solid #324353;
    background-color: #324353;
    box-shadow: 2px 2px 6px 0 #a7afb7;
    text-decoration: none !important;
    color: #fff; }

.print-btn,
.send-btn {
  border: 1px solid #324353;
  background-color: #fff;
  border-radius: 50%;
  display: block;
  height: 50px;
  width: 50px;
  cursor: pointer;
  padding: 0.75rem;
  margin-left: 1rem; }
  .print-btn svg path,
  .print-btn svg line,
  .send-btn svg path,
  .send-btn svg line {
    stroke: #324353; }
  .print-btn:hover,
  .send-btn:hover {
    background-color: #324353;
    box-shadow: 2px 2px 6px 0 #a7afb7; }
    .print-btn:hover svg path,
    .send-btn:hover svg path {
      fill: #324353; }
    .print-btn:hover path,
    .print-btn:hover line,
    .send-btn:hover path,
    .send-btn:hover line {
      stroke: #fff; }
  .print-btn i,
  .send-btn i {
    color: #343a40; }
  .print-btn svg,
  .send-btn svg {
    padding: 4px;
    height: 100%;
    width: 100%; }

.print-btn:hover i,
.send-btn:hover i {
  color: #fff; }

@media only screen and (max-width: 550px) {
  #top-header-ui {
    padding: 1rem 0.5rem; }
  .pd-0 {
    padding-right: 15px !important;
    padding-left: 15px !important; } }

#renderer-loader {
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  width: auto;
  height: auto;
  text-align: center; }
